.introCont {
  display: block;
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.introCont .semicircle {
  position: absolute;
  top: -30%;
  left: 50%;
  transform: translateX(-50%);
  height: 100vh;
  width: 200vh;
  border-radius: 0 0 150vh 150vh;
