}

.introCont .introDesc img {
  height: 40vh;
}

.introCont .introDesc .right {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-left: 1em;
}

.introCont .introDesc .right h1 {
  font-size: 3em;
  font-weight: 600;
  margin-bottom: 0.5em;
}

.introCont .introDesc .right p {
  width: 40vw;
}

.introCont .introDesc .right button {
  position: relative;
  margin: 2em 0 0 3em;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  white-space: nowrap;
  color: #fff;
  background: none;
  padding: 10px 45px;
  border-radius: 5px;
  border: none;
  font-size: 1em;
  font-weight: 500;
  outline: none;
  cursor: pointer;
  transition: 0.3s all ease;
}

.introCont .introDesc .right button i {
  position: absolute;
  animation: down 0.3s infinite alternate;
}

.introCont .introDesc .right button:hover {
  background-color: #fff;
  color: #2a9d8f;
}

.introCont .introDesc .right button::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 5px;
  animation: pulse 1s infinite alternate;
}

@keyframes down {
  from {
    margin-top: 0;
  }
  to {
    margin-top: 0.2em;
  }
}

@keyframes pulse {
  from {
    height: 100%;
    width: 100%;
    opacity: 0;
  }
  to {
    height: 150%;
    width: 110%;
    opacity: 0.5;
  }
}

