}

.introCont .flyingObjects .object3 {
  height: 50px;
  width: 50px;
  background-color: #264653;
  animation: move2 6s linear infinite alternate;
}

@keyframes move1 {
  0% {
    bottom: 5vh;
    left: 5vw;
  }
  20% {
    bottom: 7vh;
    left: 50vw;
  }
  40% {
    bottom: 25vh;
    left: 90vw;
  }
  60% {
    bottom: 80vh;
    left: 50vw;
  }
  80% {
    bottom: 50vh;
    left: 20vw;
  }
  100% {
    bottom: 10vh;
